@import url(https://fonts.googleapis.com/css2?family=Questrial&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

* {
  font-family: 'Questrial', sans-serif;
}

.container-page {
  position: relative;
  top: 90px;
  background-image: url(data:image/png;base64,);
  padding: 10px;
}

.search-container {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: #a12a2a;
  width: 90%;
  max-width: 1200px;
  margin: 10px auto;
  padding: 0 10px;
}

.search-container input,
.search-container button,
.search-container label {
  padding: 0.5rem;
  font-size: 2rem;
  border: none;
  background: white;
}

.search-container input {
  width: 100%;
}

.search-container input:focus {
  outline: 0;
}

nav {
  position: fixed;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  min-height: 90px;
  background: #313131;
  width: 100%;
  z-index: 10;
}

nav h3 {
  margin-left: 15px;
  font-size: 20px;
  line-height: 20px;
}

.menu-btn {
  background: #a12a2a;
  padding: 5px;
  margin-left: 10px;
}

.nav-links {
  top: 90px;
  position: absolute;
  display: flex;
  flex-direction: column;
  background-color: #3d3d3d;
  list-style-type: none;
  justify-content: flex-start;
  padding: 0;
  margin: 0;
  width: 200px;
  height: 90vh;
  z-index: 5;
  overflow: hidden;
}

.nav-links a {
  text-decoration: none;
  color: white;
}

.nav-links li {
  padding: 10px 0 15px;
}

.nav-links li:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.next-prev {
  display: flex;
  justify-content: center;
  align-items: center;
}

.next-prev p {
  background: #313131;
  padding: 10px 20px;
  color: #fff;
}

.next-prev button {
  padding: 6px;
  border: 0;
  background-color: #a12a2a;
  font-size: 24px;
  color: #fff;
}

.next-prev button:focus {
  outline: 0;
}

.next-prev button:disabled {
  opacity: 0;
}

.pokemon-list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0 auto;
  width: 90%;
  max-width: 1200px;
}

.pokemon-list li {
  transition: all 0.3s;
  box-sizing: border-box;
  padding: 10px 0;
  margin: 0.5%;
  list-style-type: none;
  background: #f2f2f2;
  width: 9%;
  z-index: 1;
  border-bottom: 7px solid transparent;
}

.pokemon-list li:hover {
  background: lightgray;
  border-bottom: 7px solid #a12a2a;
  cursor: pointer;
}

.pokemon-list p {
  text-transform: capitalize;
}

.pokemon-list a {
  text-decoration: none;
  color: #313131;
}

.pokemon-header {
  padding: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.pokemon-header h1 {
  text-transform: capitalize;
  margin: 0;
  padding: 0;
}

.pokemon-header p {
  margin: 0;
  padding: 0;
}

.pokemon-header img {
  max-width: 350px;
  background: #f2f2f2;
  border-radius: 50%;
  border: 5px solid #f2f2f2;
}

.stats {
  display: flex;
  flex-flow: column;
  text-align: left;
  margin: 10px;
}

.stats-info {
  background: #30a7d7;
  border-radius: 10px;
  padding: 15px;
  display: flex;
  align-items: flex-end;
  color: #f2f2f2;
}

.stats-info label {
  font-size: 110%;
  font-weight: 700;
}

.stats-info p {
  padding: 0 20px 0 5px;
  font-size: 125%;
  text-transform: capitalize;
}

.stats-info span {
  padding: 5px;
  margin: 0 5px;
  border-radius: 5px;
  text-transform: capitalize;
}

.stats-box {
  background: lightgray;
  border-radius: 10px;
  padding: 15px;
  list-style-type: none;
}

.stats-box li {
  background: linear-gradient(110deg, white 0%, white 81%, black 81%, black 100%);
  margin: 5px 0;
  padding: 7px 15px;
  border-radius: 22px;
  text-transform: capitalize;
  display: flex;
  justify-content: space-between;
  font-size: 120%;
  transition: all 0.3s;
}

.stats-box li span {
  color: #f2f2f2;
}

.stats-box li:hover {
  transform: scale(1.03);
}

.navPokemon {
  display: flex;
  justify-content: center;
  background: #f2f2f2;
}

.navPokemon .btnNavPokemon {
  background: #f16e57;
  background: linear-gradient(180deg, #f16e57 0%, #f16e57 45%, black 45%, black 55%, whitesmoke 55%, whitesmoke 100%);
  height: 40px;
  width: 40px;
  border-radius: 25px;
  margin: 0 10px;
  position: relative;
  border: 3px solid black;
}

.navPokemon .btnNavPokemon svg {
  background: whitesmoke;
  border: 3px solid black;
  border-radius: 15px;
  position: absolute;
  margin: auto;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.navPokemon .btnNavPokemon:hover {
  -webkit-animation: spinn 0.2s forwards;
          animation: spinn 0.2s forwards;
}

.fadeAnimation {
  opacity: 0;
  -webkit-animation: fade-effect 0.5s forwards;
          animation: fade-effect 0.5s forwards;
}

@-webkit-keyframes fade-effect {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade-effect {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@-webkit-keyframes spinn {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(20deg);
  }
}

@keyframes spinn {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(20deg);
  }
}

@media screen and (max-width: 1100px) {
  .pokemon-list li {
    width: 19%;
  }
}

/*
@mixin bgType($value) {
    background-color: $value;
}

@mixin bgType2($value1, $value2...) {
    background: rgb(0, 0, 0);
    background: linear-gradient(180deg, $value1 50%, $value2 50%);
}
*/
@media screen and (max-width: 800px) {
  .pokemon-header {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0;
  }
  .pokemon-list li {
    width: 49%;
  }
  form label {
    display: none;
  }
  .stats,
  .stats-info,
  .stats-box {
    width: 93%;
    margin: 10px 0;
  }
  .stats-info {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: left;
  }
  .stats-info label {
    font-size: 110%;
    font-weight: 700;
  }
  .stats-info p {
    margin: 5px 0 10px;
    padding: 0;
  }
  .stats-info span {
    margin: 10px 10px 0 0;
  }
}

